.btn {
  display: inline-block;
  padding: 15px 35px 17px;
  font-family: $font-base-book;
  font-weight: normal;
  font-size: 20px;
  color: #fff;
  background: $color-link;
  border-radius: 50px;
  border: none;
  transition: opacity ease-in-out .2s;

  &:hover {
    opacity: .9;
  }

  &:focus {
    outline: 3px dotted #EB3F57;
  }

}
